/*
 *   Name: Bizagi Render Base Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define a base render class with common stuff related to all renders
 */

$.Class.extend("bizagi.rendering.render.base", {}, {

    /**
     * Method to process the render the layout
     * @param {boolean} isLabelFormat
     * @param {string} format
     */
    processLayout: function (isLabelFormat, format) {
        var properties = this.properties;

        if (typeof isLabelFormat !== "boolean") {
            console.warm("error calling processLayout, 'isLabelFormat' is not boolean");
            return;
        }

        if (isLabelFormat) {
            this.processLabelLayout(format);
        } else {
            this.processControlLayout(format);

            // Set required and visiblity
            properties.required = typeof (properties.required) !== "undefined" ? bizagi.util.parseBoolean(properties.required) : false;
            var visible = typeof (properties.visible) !== "undefined" ? bizagi.util.parseBoolean(properties.visible) : true;

            if (properties.required) {
                this.changeRequired(properties.required);
            }

            if (!visible) {
                this.changeVisibility(visible);
            }
        }
    },

    /**
     * Method to process the label in the layout
     * @param {} format
     * @returns {}
     */
    processLabelLayout: function (format) {
        var $label = this.getLabel();
        if ($label == null) return;

        // Set customizations
        format = format || {};
        if (typeof format.bold === "undefined") {
            format.bold = true; // Default value
        }

        this.changeBackgroundColor(format.background)
            .changeFontColor($label, format.color)
            .changeFontBold($label, format.bold)
            .changeFontSize($label, format.size)
            .changeFontItalic($label, format.italic)
            .changeTextDecoration($label, (format.underline ? "underline" : "") + (format.strikethru ? " line-through" : ""));
    },

    /**
     * Method to process the control in the layout
     * @param {} format
     * @returns {}
     */
    processControlLayout: function (format) {
        var $control = this.getControl();
        var $elements = this.getElementsToStylize($control);

        // Set customizations
        format = format || {};

        this.changeFontColor($elements, format.color)
            .changeFontBold($elements, format.bold)
            .changeFontItalic($elements, format.italic)
            .changeFontSize($elements, format.size)
            .changeTextDecoration($elements, (format.underline ? "underline" : "") + (format.strikethru ? " line-through" : ""));
    },

    /**
     * Customizes render color
     * @param {} color
     * @returns {}
     */
    changeColor: function (color) {
        var $label = this.getLabel();
        var $control = this.getControl();
        var $elements = this.getElementsToStylize($control);

        return this
            .changeFontColor($label, color)
            .changeFontColor($elements, color);
    },

    /**
     * Customizes render color
     * @param {} $element
     * @param {} color
     * @returns {}
     */
    changeFontColor: function ($element, color) {
        if ($element === undefined) return this;
        if (color === undefined) return this;

        var style = color !== "none" ? color : "";
        $element.css("color", style);

        return this;
    },

    /**
     * Customizes the font size property
     * @param {} $element
     * @param {} size
     * @returns {}
     */
    changeFontSize: function ($element, size) {
        if (size === undefined) return this;
        if ($element === undefined) return this;

        if (size !== "0") {
            var newFontSize = 100 + Number(size) * 10 + "%";
            $element.css("font-size", newFontSize);
        }

        return this;
    },

    /**
     * Customizes the font Strikethrough or underline property
     * @param {} $element
     * @param {} textDecoration
     * @returns {}
     */
    changeTextDecoration: function ($element, textDecoration) {
        if (textDecoration === undefined) return this;
        if ($element === undefined) return this;

        textDecoration && $element.css("text-decoration", textDecoration);

        return this;
    },

    /**
     * Customizes the font italic style
     * @param {} $element
     * @param {} italic
     * @returns {}
     */
    changeFontItalic: function ($element, italic) {
        var style = bizagi.util.parseBoolean(italic) ? "italic" : "normal";

        $element.css("font-style", style);

        return this;
    },

    /**
     * Customizes the font weight
     * @param {} $label
     * @param {} bold
     * @returns {}
     */
    changeFontBold: function ($label, bold) {
        var style = bizagi.util.parseBoolean(bold) ? "bold" : "normal";

        $label.css("font-weight", style);

        return this;
    },

    /**
     * Customizes render background color
     * @param {} color
     * @returns {}
     */
    changeBackgroundColor: function (color) {
        if (typeof color === "undefined") return this;

        if (color !== "none") {
            this.element.css("background-color", color);
        } else {
            this.element.css("background-color", "");
        }

        return this;
    },

    /**
     * This method just make a toggle of red label indicator
     * @argument {boolean} argument
     */
    changeRequiredLabel: function () {
    },

    /**
     * Change cell required and attach bindings
     * @param argument
     */
    changeCellRequired: function (argument) {
        this.changeRequired(argument);
    },

    /**
     * 
     */
    getRTLAlign: function (align) {
        if (typeof align == "undefined" || align == "") {
            return "default";
        }
        return align;
    },

    /**
     * TODO: This override should be removed after genome, just because the align shoudnt be depend of the display type
     */
    getDefaultLabelAlign: function(){
        return "default";
    },

    /**
     * TODO: This override should be removed after genome, just because the align shoudnt be depend of the display type
     */
    getDefaultValueAlign: function(){
        return "default";
    }
});