/**
*   Name: Bizagi Render Image Class
*   Author: UI Forms Team
*   Comments:
*   -   This script will define basic stuff for image control
*/

$.Class.extend("bizagi.rendering.image", {
    // Statics
    BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
    BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
    BA_PAGE_CACHE: bizagi.render.services.service.BA_PAGE_CACHE
}, {

    /**
     * Update or init the element data
     * @param data
     */
    initializeData: function (data) {
        var self = this;

        // Call base
        self._super(data);

        // Fill default properties
        var properties = this.properties;
        var maxSize = 4091904;

        if(properties.maxSize) {
            maxSize = Number(properties.maxSize);
        } else if (typeof (BIZAGI_SETTINGS) !== "undefined" && typeof (BIZAGI_SETTINGS.UploadMaxFileSize) !== "undefined") {
            maxSize = Number(BIZAGI_SETTINGS.UploadMaxFileSize);
        }

        properties.maxSize = maxSize;
        properties.editable = bizagi.util.parseBoolean(properties.editable) === true;
        properties.validExtensions = properties.validExtensions || "";

        if (properties.validExtensions.length > 0 && properties.validExtensions.indexOf(".") < 0) {
            var singleExtensions = properties.validExtensions.replace(/\ /gi, '').split(";");

            for (var i = 0; i < singleExtensions.length; i++) {
                if (singleExtensions[i].length != 0) {
                    singleExtensions[i] = "*." + singleExtensions[i].replaceAll(" ", "");
                } else {
                    //Removes the empty element
                    var index = singleExtensions.indexOf(singleExtensions[i]);
                    singleExtensions.splice(i, 1);
                }
            }

            properties.validExtensions = singleExtensions.join(";");
        }

        properties.addUrl = properties.addUrl || self.dataService.getUploadAddFileUrl(properties.isUserPreference);
        properties.allowDelete = bizagi.util.parseBoolean(properties.allowDelete) !== null;

        properties.isAutoWidth = false;
        properties.isAutoHeight = false;

        if (properties.width === -1) {
            properties.width = "100%";
            properties.isAutoWidth = true;
        } else {
            properties.width = properties.width + "px";
        }

        if (properties.height === -1) {
            properties.height = "auto";
            properties.isAutoHeight = true;
        } else {
            properties.height = properties.height + "px";
        }
    },

    /**
     * Template method to implement in each children to customize each control
     * @returns {*}
     */
    renderControl: function () {
        var self = this;
        var def = new $.Deferred(); //design
        var template = self.renderFactory.getTemplate("image");        

        $.when(self.buildItemUrl()).done(function (dataUrl) {
            if (!self.isDisposed()) {
                self.properties.url = dataUrl !== "" ? dataUrl : "";
                self.properties.value = self.value = self.properties.url.length > 0 ? [self.properties.url] : null;
                self.setValue(self.properties.value);                

                var html = $.fasttmpl(template, { url: self.properties.url });
                def.resolve(html);
            }
        }).fail(function (error) {
           def.reject(error)
        });

        return def.promise();
    },

    /**
     * Method to render non editable values
     * @returns {*}
     */
    renderReadOnly: function () {
        var template = this.renderFactory.getTemplate("image-readonly");
        var html = $.fasttmpl(template);

        return html;
    },

    /**
     * This method return the name of template of items, please make a override in each device
     * @returns {*}
     */
    getTemplateItem: function () {
        return this.renderFactory.getTemplate("image-item");
    },

    /**
     * Renders a single upload item
     * @param file
     * @returns {*}
     */
    renderUploadItem: function (file) {
        var self = this;
        var mode = self.getMode();        
        var def = new $.Deferred();
        var template = self.getTemplateItem();

        $.when(self.buildItemUrl()).done(function (dataUrl) {
            var url = dataUrl || "";

            var html = $.fasttmpl(template, {
                url: url,
                allowDelete: self.properties.allowDelete,
                editable: self.properties.editable,
                mode: mode,                
            });

            self.properties.url = url;
            self.properties.value = url.length > 0 ? [url] : null;

            def.resolve(html);
        });

        return def.promise();
    },

    /**
     * Builds the upload item url
     * @param params
     * @returns {*}
     */
    buildItemUrl: function (params) {
        var self = this;
        var properties = self.properties;
        var def = new $.Deferred();
        var url = "";

        if (!this.isExecutionMode()) {
            url = ""; // TODO: Review the use of "javascript:void(0);";
            def.resolve(url);
        } else {
            var parameters = {
                url: properties.dataUrl,
                xpath: properties.xpath,
                idRender: properties.id,
                xpathContext: properties.xpathContext,
                idPageCache: properties.idPageCache,
                property: params && params.property ? params.property : "fileContent",
                dataType: "text"
            };

            if (properties.contexttype) {
                parameters.contexttype = properties.contexttype;
            }

            if (properties.isUserPreference) {
                self.currentUser = bizagi.currentUser.idUser;
                parameters.xpathContext = "@Context.Users[id == " + self.currentUser + "]";
                parameters.contexttype = "entity";
                parameters.surrogatekey = self.currentUser;
            }

            self.dataService.multiaction().getPropertyData(parameters, "text")
                .done(function (data) {
                    if (data.charAt(0) === "\"") {
                        data = data.slice(1, -1);
                    }

                    if (data !== "") {
                        url = "data:image/png;base64," + data;
                    }

                    def.resolve(url);
                }).fail(function (error) {
                def.reject(error);
            });
        }

        return def.promise();
    },

    buildItemUrlOffline: function(parameters){
        var def = new $.Deferred();
        var self = this;
        var properties = self.properties;
        var formParams = self.getFormParams();
        var loadImageFromForm = true;
        var imageId = properties.xpath;
        if( properties.offlineImageId ){
            imageId = properties.offlineImageId;
            loadImageFromForm = false;
        }
        var parameters = {
            idCase: formParams.idCase,
            idWorkitem: formParams.idWorkitem,
            id: imageId,
            offlineType: formParams.offlineType,
            loadImageFromForm: loadImageFromForm
        }
        $.when(self.dataService.getOfflineImage(parameters, "base64"))
        .done( function(imageValue){
            properties.value = imageValue;
            def.resolve(bizagi.util.getDataUrl(properties));
        });
        return def.promise();
    },

    /**
     * Create an image offline
     * @param path File in Browser or Path in Mobile Devices
     * @param promise Promise to be resolved when save is completed
     */
    saveImageOffline: function (path) {
        var self = this; 
        var def = new $.Deferred();       
        var blobPromise = new $.Deferred();
        var offlineImageId = self.properties.xpath + '-' + bizagi.util.randomNumber();

        if(self.isNative){
            var fileName = self.fileProperties.name;            
            if (!bizagi.util.isAndroidDevice()) {
                path = bizagi.util.media.normalizeIOSFilePath(path);
            }
            else{
                // Fix android 4.4 getting images from recent folder
                path = bizagi.util.media.getImagePath(path);
            }
            
            var errorHandler = function (error) {
                bizagi.log('error converting path to blob', error);  
                blobPromise.reject(error);
            };
    
            window.resolveLocalFileSystemURL(path, function (fileEntry) {
                fileEntry.file(function (file) {    
                    var reader = new FileReader();
                    reader.onloadend = function () {
                        var blob = new Blob([this.result], {type: file.type});
                        blob.name = fileName;
                        blobPromise.resolve(blob);
                    }    
                    reader.readAsArrayBuffer(file);
                }, errorHandler);
            }, errorHandler);
        }
        else {
            blobPromise.resolve(path);
        }        

        $.when(blobPromise)
        .done( function(blobImage) {
            // Save the image as attachment 
            var formParams = self.getFormParams();
            var parameters = {
                idCase: formParams.idCase,
                idWorkitem: formParams.idWorkitem,
                id: offlineImageId,
                operationType: "edition",
                offlineType: formParams.offlineType
            }

            $.when(self.dataService.saveOfflineImage(parameters, blobImage))
            .done( function() {
                self.properties.offlineImageId = offlineImageId;
                def.resolve();
            })
            .fail( function(err){
                def.reject(err);
            });
        })
        .fail( function(blobError){
            def.reject(blobError);
        });

        return def.promise();
    },

    /**
     * Builds the upload item url in real size
     * @returns {*}
     */
    buildFullItemUrl: function () {
        var def = new $.Deferred();

        $.when(this.buildItemUrl({"property": "fileFullContent"}))
            .done(function (dataUrl) {
                def.resolve(dataUrl);
            }).fail(function (error) {
            def.reject(error);
        });

        return def.promise();
    },

    /**
     * Method to determine if the render value can be sent to the server or not
     * @returns {boolean}
     */
    canBeSent: function () {
        // This render cannot be sent because it is full ajax
        return false;
    },

    /**
     * Sets the internal value
     * @param value
     * @param triggerEvents
     */
    setValue: function (value, triggerEvents) {
        // Call base
        this._super(value, triggerEvents);
    },

    /**
     * Returns the internal value
     * @returns {*|Array}
     */
    getValue: function () {
        return this.properties.value || [];
    },

    /**
     * Check if control is valid or not
     * @param invalidElements
     * @returns {boolean}
     */
    isValid: function (invalidElements) {
        var self = this;

        // Don't check non-editable renders
        if (bizagi.util.parseBoolean(self.properties.visible) === false) {
            return true;
        }

        // Don't check non- editable renders
        if (bizagi.util.parseBoolean(self.properties.editable) === false) {
            return true;
        }

        // Clear error message
        self.setValidationMessage("");

        // Check required
        if (self.properties.required) {
            var newRow = self.grid && self.grid.isFromCreatedRow.apply(self);

            if (!newRow) {
                var value = self.getValue();

                if (value.length === 0) {
                    var message = self.getResource("render-required-text").replaceAll("#label#", self.properties.displayName);
                    invalidElements.push({ xpath: self.properties.xpath, message: message });
                    return false;
                }
            }
        }

        return true;
    },

    /**
     * Build add params to send to the server
     */
    buildAddParams: function () {
        var self = this;
        var properties = self.properties;
        var form = self.getFormContainer();
        var data = {};

        data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "xpath"] = self.getUploadXpath();
        data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "idRender"] = properties.id;
        data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "xpathContext"] = properties.xpathContext;
        data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + self.Class.BA_PAGE_CACHE] = properties.idPageCache;
        data[self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId"] = form.properties.sessionId;
        data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "action"] = 'savefile';

        if (properties.contexttype) {
            data[self.Class.BA_CONTEXT_PARAMETER_PREFIX + "contexttype"] = properties.contexttype;
        }

        try {
            if (typeof (BIZAGI_SESSION_NAME) !== "undefined") {
                data[BIZAGI_SESSION_NAME] = form.properties.sessionId;
            } else {
                data["JSESSIONID"] = form.properties.sessionId;
            }
        } catch (e) {
            data["JSESSIONID"] = form.properties.sessionId;
        }

        return data;
    },

    /**
     * Returns the xpath to be used
     * @returns {*}
     */
    getUploadXpath: function () {
        return this.properties.xpath;
    },

    /**
     * Clean html because some scenarios render twice
     */
    emptyControlByAsyncRenderDeferred: function () {
        var $control = this.getControl();
        $control.empty();
    }
});