(function ($) {

    $.fn.material_bizagi_tabs = function (options, params) {

        var opts = $.extend({}, $.fn.material_bizagi_tabs.defaults, options);

        var base = {
            index: 0,
            tabs: [],
            $el: undefined,
            $active: undefined,
            $active_content: undefined, 
            init: function(el, params){
                var self = this;
                var $tabs = $(">ul.bz-tabs", el);
                var $content_tabs = self.$el = $(el);
                var $ind;
                self.index = opts.active_index_tab;

                function updatePositions(active, $indicator){
                    if (typeof $indicator !== "undefined"){
                        var info = updateIndicatorState($tabs.get(0), active.get(0), opts.direction);
                        $indicator.css("left", info.left).css("width", info.width);
                    }
        
                    var scroll_info = scrollSelectedIntoView($tabs.get(0), active.get(0), opts.direction);                        
                    $tabs.scrollLeft(scroll_info);
                    $tabs.trigger("updatePositions", getTabsMetaInfo($tabs.get(0), active.get(0), opts.direction));
                }

                $(">li.bz-tab", $tabs).each(function(index, element) {
                    if (index === opts.active_index_tab){
                        self.$active = $(this);
                        self.$active.addClass("active");
                    }
                    else {
                        $($(element).data("reference"), $content_tabs).hide();
                    }
                    self.tabs.push({index: index, element: element});
                });
                
                self.$active_content = $(self.$active.data("reference"), $content_tabs);
                
                //Updates the initial position
                updatePositions(self.$active);
                
                $tabs.on('click', '> li.bz-tab', function (e) {
                    e.stopPropagation();
        
                    if (typeof $ind === "undefined"){
                        $ind = $("<li class='bz-indicator-bar'></li>");
                        var info = updateIndicatorState($tabs.get(0), self.$active.get(0), opts.direction);
                        $ind.css("left", info.left).css("width", info.width);
                        $ind = $tabs.prepend($ind).find(".bz-indicator-bar");
                    }
        
                    self.$active.removeClass('active');            
                    self.$active_content.hide();
        
                    self.$active = $(this);
        
                    var reference = self.$active.data("reference");
                    self.index = self.$active.data("index");

                    self.$active_content = $(reference, $content_tabs);
        
                    self.$active.addClass('active');

                    //Updates to the current position
                    updatePositions(self.$active, $ind);
                    $tabs.trigger("updateActive", {index: self.index});

                    self.$active_content.show();
                    e.preventDefault();
                });

            },
            getActiveTab: function(){
                return {
                    index: this.index,
                    tab: this.$active,
                    contantTab : this.$active_content
                };
            },
            moveToNextTab: function(){
                var index = ((this.index + 1) < this.tabs.length) ? this.index + 1 : 0;
                this.tabs[index].element.click();
            },
            refreshPositions: function(){
                this.tabs[this.index].element.click();
            }
        };

        
        if (typeof options == "string" && options) {
            base = $(this).data("base");
            if (typeof base == "undefined"){
                console.error("this plugin is has not been initialized!!");
            }
            return base[options](params);
        }
        else {
            return this.each(function () {
                base.init(this, params);
                $(this).data("base", base);
            });
        }
    };

    $.fn.material_bizagi_tabs.defaults = {
        direction: 'ltr',
        active_index_tab: 0
    }

    function updateIndicatorState(tabs, active_tab, direction) {
        var tabs_meta = getTabsMetaInfo(tabs, active_tab, direction);
        var tabs_info = tabs_meta.tabsMetaInfo;
        var tab_info = tabs_meta.tabMetaInfo;

        var left = 0;
        if (tabs_info && tab_info) {
            var tmp_left = "rtl" === direction ? tabs_info.scrollLeftNormalized + tabs_info.clientWidth - tabs_info.scrollWidth : tabs_info.scrollLeft;
            left = tab_info.left - tabs_info.left + tmp_left;
        }
        return {
            left: left,
            width: tab_info ? tab_info.width : 0
        }
    }

    function getTabsMetaInfo(tabs, active_tab, direction) {
        var tabs_meta_info = void 0;
        if (tabs) {
            var r = tabs.getBoundingClientRect();
            tabs_meta_info = {
                clientWidth: tabs ? tabs.clientWidth : 0,
                scrollLeft: tabs ? tabs.scrollLeft : 0,
                scrollLeftNormalized: tabs ? getNormalizedScrollLeft(tabs, direction) : 0,
                scrollWidth: tabs ? tabs.scrollWidth : 0,
                left: r.left,
                right: r.right
            }
        }
        var tab_meta_info = void 0;
        if (tabs && false !== active_tab) {
            tab_meta_info = active_tab.getBoundingClientRect();
        }

        return {
            tabsMetaInfo: tabs_meta_info,
            tabMetaInfo: tab_meta_info
        }
    }

    function getNormalizedScrollLeft(tabs, direction) {
        var a = tabs.scrollLeft;
        if ("rtl" !== direction)
            return a;
        var n = getScrollType(tabs);
        if ("indeterminate" === n)
            return Number.NaN;
        switch (n) {
            case "negative":
                return e.scrollWidth - e.clientWidth + a;
            case "reverse":
                return e.scrollWidth - e.clientWidth - a
        }
        return a
    }

    function getScrollType(tabs) {
        if (!tabs || !window.document.body)
            return "indeterminate";

        var e = window.document.createElement("div");
        e.appendChild(document.createTextNode("ABCD"));
        e.dir = "rtl";
        e.style.fontSize = "14px";
        e.style.width = "4px";
        e.style.height = "1px";
        e.style.position = "absolute";
        e.style.top = "-1000px";
        e.style.overflow = "scroll";
        document.body.appendChild(e);
        type = "reverse";
        if (e.scrollLeft > 0)
            type = "default";
        else {
            e.scrollLeft = 1;
            0 === e.scrollLeft && (type = "negative")
        }
        document.body.removeChild(e);
        return type;
    }

    scrollSelectedIntoView = function (tabs, active_tab, direction) {
        var tabs_meta = getTabsMetaInfo(tabs, active_tab, direction)
        var tabs_info = tabs_meta.tabsMetaInfo;
        var tab_info = tabs_meta.tabMetaInfo;

        if (!tab_info || !tabs_info)
            return;
        
        var scroll_info = void 0;
        if (tab_info.left < tabs_info.left) {
            scroll_info = tabs_info.scrollLeft + (tab_info.left - tabs_info.left);
        } else if (tab_info.right > tabs_info.right) {
            scroll_info = tabs_info.scrollLeft + (tab_info.right - tabs_info.right);
        }             
        return scroll_info;
    }
    
})($);