/*
*   Name: Bizagi Grid for tablet
*   Author: UI Forms Team
*   Comments:
*   -   This widget will extend the bizagi grid widget to specialize into a tablet view
*
*   Markup: <div></div>
*
*   Styles:
*   - css/bizagi/tablet/controls/bizagi.grid.css
*/

(function ($) {
    $.widget("ui.bizagi_grid_tablet", $.ui.bizagi_grid, {

        /**
         * Constructor
         * @private
         */
        _init: function () {
            this._super();

            this.isOfflineForm = typeof (this.options.isOfflineForm) !== "undefined" && bizagi.util.parseBoolean(this.options.isOfflineForm);
        },

        /**
         * Method to attach any handler to the grid
         * @returns {}
         */
        attachHandlers: function () {
            var self = this;

            // Add paging events
            self._getComponent("pager").find("ul li button").bind("click", function () {
                var pageNumber = parseInt($(this).attr("data-page-number"));

                if (pageNumber) {
                    self.changePage(pageNumber);
                }
            });

            //TODO falta implementar
            self._getComponent("dynamicPager").find("input").bind("click", function () {
                self.selectDynamicPager();
            });

            // Add sort events
            self._getComponent("columns").find(".bz-tablet-grid__label").bind("click", function () {
                if (!self.isOfflineForm) {
                    var columnIndex = $(this).attr("data-column-index");
                    self.sortBy(columnIndex);
                }
            });

            // Bind button actions
            self._getComponent("buttons").find("[data-action]").bind("click", function () {
                if ($(this).attr("data-action") === "totalizer") {
                    self.totalizer();
                } else {
                    self.processAction($(this).attr("data-action"));
                    self.unselectRow();
                }
            });

            self._getComponent("rowButtons").find("[data-action]").bind("click", function () {
                self.processAction($(this).attr("data-action"));
                self.unselectRow();
            });

            if (this.isOfflineForm) {
                self._getComponent("rows").on("click", ".bz-tablet-grid__cell-more [data-action=more]", function (evt) {
                    var $context = $(evt.target).parents("[data-bizagi-component=cells]");

                    if ($context && $context.hasClass("bz-tablet-grid__row")) {
                        var businessKey = $context.attr("data-business-key");
                        var isNewRow = bizagi.util.parseBoolean($context.attr("data-new-row")) || false;

                        if (isNewRow) {
                            self.onRemoveNewRow(businessKey);
                        } else {
                            self.processAction("remove", {key: businessKey});
                        }
                    }
                });
            } else {

                self._getComponent("rows").find("tr").bind("click", function (evt) {
                    var $context = $(evt.target).closest(".bz-tablet-grid__cell");

                    evt.stopPropagation();

                    if ($context && $context.hasClass("bz-tablet-grid__cell")) {
                        var key = $(this).attr("data-key");
                        var businessKey = $(this).attr("data-business-key");

                        self.selectRow(key, businessKey);
                    }
                });

                // Bind special cell button action
                self._addColumnActions();
            }
        },

        /**
         * Popup Total
         * @returns {}
         */
        totalizer: function () {
            this.options.totalData();
        },
        /**
         * Executes the row selected handler
         * @returns {}
         */
        onRowSelected: function () {
            // Call base
            $.ui.bizagi_grid.prototype.onRowSelected.apply(this, arguments);

            // Show row buttons             
            this._showRowButtons();
        },

        /**
         * Executes the row un-selected handler
         * @returns {}
         */
        onRowUnselected: function () {
            // Call base
            $.ui.bizagi_grid.prototype.onRowUnselected.apply(this, arguments);

            // Hide row buttons           
            this._hideRowButtons();
        },

        getCell: function (key, xpath, index) {
            var self = this;
            var row = self._getComponent("rows").find("[data-business-key=" + key + "]");
            var allVisibleTd, cell = "";

            if (index < 4) {
                allVisibleTd = $("td", $(row));
                cell = allVisibleTd[index + 1];
            } else {
                allVisibleTd = $("td:visible", $(row));
                cell = allVisibleTd[index];
            }

            return $(cell);
        },

        /**
         * Template method to check if buttons component must be rendered
         * @returns {}
         */
        _canShowButtons: function () {
            var actions = this.options.actions;

            if (this.isOfflineForm) {
                return actions["add"] || actions["remove"] || actions["edit"] || actions["inlineAdd"];
            } else {
                return actions["add"] || actions["hasTotalizer"];
            }
        },

        /*
        *   This method draws a grid row
        */
        drawRow: function (rowData, isNewRow) {
            var self = this;
            var options = self.options;
            var key = rowData[self.columnKeyIndex];
            var newRow = isNewRow || false;
            var rnd = Math.floor(Math.random() * 10000);
            var row = $.fasttmpl(options.template.row, {
                key: rnd,
                isNewRow: isNewRow,
                businessKey: key
            });

            // Resolve cells
            var cells = "";
            $.each(rowData, function (i, cell) {
                // Draw normal cell
                cells += self.drawCell(i, key, cell, newRow);
            });

            if (self._canShowRowButtons()){
                // Draw special column
                cells += self.drawSpecialCell(self.options.columns.length + 1);
            }

            // Resolve cells template
            row = self._replaceElement(row, "cells", cells);
            return row;
        },

        /*
        *   This method draws a grid pager
        */
        drawPager: function () {
            var self = this;
            var options = self.options;

            // Calculate pages
            var pages = [];
            var renderPages = [];

            for (var i = 1; i <= self.data.total; i++) {
                pages.push({
                    number: i,
                    active: (i == self.data.page)
                });
            }

            if (self.data.page < 3 || self.data.page > self.data.total - 2) {
                renderPages = [];
            } else {
                renderPages = [self.data.page - 1, self.data.page, self.data.page + 1];
            }

            var pagination = {
                renderPages: renderPages,
                pages: pages,
                pagination: {
                    totalPages: self.data.total,
                    currentPage: self.data.page
                }
            };

            // Resolve pager template
            return $.fasttmpl(options.template.pager, pagination);
        },

        /**
         * Template method to check if row buttons must be displayed when the row is selected
         * @returns {}
         */
        _canShowRowButtons: function () {
            var actions = this.options.actions;

            if (this.isOfflineForm) {
                return false;
            } else {
                return actions["edit"] || actions["details"] || actions["remove"] || this.options.subType == "adhocGrid";
            }
        },

        /**
         * Show the row buttons
         * @returns {}
         */
        _showRowButtons: function () {
            var self = this;
            var doc = window.document;
            var element = self.element;

            // Add selected style to row
            var tr = element.find("[data-bizagi-component=rows] tr[data-key=" + self.selectedRow + "]");
            tr.addClass("ui-bizagi-state-selected");
        },

        /**
         * Hide the row buttons
         * @returns {}
         */
        _hideRowButtons: function () {
            var self = this;

            // Call base
            $.ui.bizagi_grid.prototype.onRowUnselected.apply(self, arguments);

            var tr = self._getComponent("rows").find("tr[data-key=" + self.selectedRow + "]");
            tr.removeClass("ui-bizagi-state-selected");

            // Hide floating buttons for the row
            if (!self.isOfflineForm) {
                var floatingButtons = self._getComponent("rowButtons");
                floatingButtons.hide();
            }
        },

        /**
         * Return the new created ids
         * @param {} rows
         * @returns {}
         */
        getNewRowKeys: function (rows) {
            var self = this;

            rows = rows || self._getComponent("rows").find("[data-new-row=true]");

            return $.map(rows, function (row, i) {
                return $(row).data("business-key");
            });
        },

        /**
         * Process an action in order to do something with the grid
         * @param {} action
         * @returns {}
         */
        processAction: function (action) {
            var self = this;

            // Call base
            $.ui.bizagi_grid.prototype.processAction.apply(self, arguments);

            if (action === "save") {
                self.onSaveAddedRows();
            }
            if (action === "cancel") {
                self.onCancelEdition();
            }
        },

        /**
         * Check if row its new
         * @param {} businessKey
         * @returns {}
         */
        isNewRecord: function (businessKey) {
            var self = this;
            var row = self._getComponent("rows");

            if (row) {
                businessKey = row.find("[data-business-key='" + businessKey + "']");
                if ($(businessKey).data("new-row")) {
                    return true;
                }
            }
            return false;
        },

        /**
         * Add inline row
         * @returns {}
         */
        addInlineRow: function () {
            var self = this;

            // Draw the new row
            self._triggerHandler("beforeInlineAdd");
            var html = self.drawRow(self.createAddRowData(), true);
            var newRow = $(html);

            // Execute the postrender
            self.postRenderRow(newRow);

            // Append to rows component
            self._getComponent("rows").append(newRow);

            var buttons = self._getComponent("buttons");

            if (self.isOfflineForm) {
                buttons.find("[data-action=save]").removeClass("bz-tablet-grid__buttons--hidden");
                buttons.find("[data-action=cancel]").removeClass("bz-tablet-grid__buttons--hidden");
            } else {
                buttons.find("[data-action=save]").css("display", "inline-block");
                buttons.find("[data-action=cancel]").css("display", "inline-block");
            }

            // Show table component if there are no rows
            if (self.data.rows.length === 0) {
                self._getComponent("table").show();
                self._getComponent("emptyTable").hide();
            }

            self._triggerHandler("afterInlineAdd");
        },

        /**
         * Creates a dummy data for the new row
         * @returns {}
         */
        createAddRowData: function () {
            var self = this;
            var options = self.options;
            var row = [];

            $.each(options.columns, function (i) {

                if (i === self.columnKeyIndex) {
                    // Add random key so we can identify them later
                    var key = Math.ceil(Math.random() * 100000);
                    row.push(key);
                } else {
                    // Add null value
                    row.push(null);
                }
            });

            return row;
        },

        /**
         * Executes the on add row handler
         * @returns {}
         */
        onAddRow: function () {
            var self = this;

            // Allow add
            if (self.options.actions.add) {
                if (self.options.actions.inlineAdd) {
                    return self.addInlineRow();
                } else {
                    // Open dialog with addform
                    return self._triggerHandler("addRow");
                }
            }
        },

        /**
         * Executes when the user presses the cancel button, after inline add edition
         * @returns {}
         */
        onCancelEdition: function () {
            var self = this;

            // Remove zero key rows
            var rows = self._getComponent("rows").find("[data-new-row=true]");
            var savedRows = self._getComponent("rows").find("[data-new-row=false]");
            rows.detach();

            // Hide buttons
            var buttons = self._getComponent("buttons");

            if (self.isOfflineForm) {
                buttons.find("[data-action=save]").addClass("bz-tablet-grid__buttons--hidden");
                buttons.find("[data-action=cancel]").addClass("bz-tablet-grid__buttons--hidden");
            } else {
                buttons.find("[data-action=save]").hide();
                buttons.find("[data-action=cancel]").hide();
            }

            // Show table component if there are no rows
            if (savedRows.length === 0) {
                self._getComponent("table").hide();
                self._getComponent("emptyTable").show();
            }

            return self._triggerHandler("cancelEdition", {keys: self.getNewRowKeys(rows)});
        },

        /**
         * Executes when the user presses the save button, after inline add edition
         * @returns {}
         */
        onSaveAddedRows: function () {
            var self = this;

            // Retrieve key of added rows
            var newRowKeys = self.getNewRowKeys();
            var buttons = self._getComponent("buttons");

            // Hide buttons         
            if (newRowKeys.length >= 1 && self.isValid()) {

                var row = self._getComponent("rows");

                if (self.isOfflineForm) {
                    $.each(newRowKeys, function (key, value) {
                        var businessKey = row.find("[data-business-key='" + value + "']");
                        $(businessKey).attr("data-new-row", 'false');
                    });

                    buttons.find("[data-action=save]").addClass("bz-tablet-grid__buttons--hidden");
                    buttons.find("[data-action=cancel]").addClass("bz-tablet-grid__buttons--hidden");

                    self._triggerHandler("saveAddedRows", {keys: newRowKeys});

                    return true;
                } else {
                    buttons.find("[data-action=save]").hide();
                    buttons.find("[data-action=cancel]").hide();

                    self._triggerHandler("saveAddedRows", {keys: newRowKeys});

                    return self._triggerHandler("removeNewRecords", {keys: newRowKeys});
                }

            } else {
                return true;
            }
        },

        /**
         * Add new column actions
         * @private
         */
        _addColumnActions: function () {
            var self = this;
            var items = [];

            if (self.options.actions.details) {
                items.push({
                    name: bizagi.localization.getResource("render-collection-navigator-details-form"),
                    fn: function (e) {
                        var businessKey = $(e).parents("[data-bizagi-component=cells]").attr("data-business-key");

                        if (self.options.actions.details) {
                            self.processAction("details", {key: businessKey});
                        } else {
                            self.processAction("more", {key: businessKey});
                        }
                    }
                });
            }

            if (self.options.actions.edit) {
                items.push({
                    name: bizagi.localization.getResource("render-grid-edit-label"),
                    fn: function (e) {
                        var businessKey = $(e).parents("[data-bizagi-component=cells]").attr("data-business-key");

                        self.processAction("edit", {key: businessKey});
                    }
                });
            }

            if (self.options.actions.remove) {
                items.push({
                    name: bizagi.localization.getResource("render-ecm-confirm-bt-delete"),
                    fn: function (e) {
                        var businessKey = $(e).parents("[data-bizagi-component=cells]").attr("data-business-key");

                        self.processAction("remove", {key: businessKey});
                    }
                });
            }

            self._getComponent("rows").find("[data-action=more]").overflowMenu({items: items});
        },

        /**
         * Executes when the user presses the cancel button, after inline add edition
         * @param key
         * @param businessKey
         */
        onRemoveNewRow: function (businessKey) {
            var self = this;
            var buttons = self._getComponent("buttons");

            // Remove zero key rows
            var cancelNewRow = self._getComponent("rows").find("tr[data-business-key=" + businessKey + "]");
            var savedRows = self._getComponent("rows").find("[data-new-row=false]");
            var newRows = self._getComponent("rows").find("[data-new-row=true]");

            cancelNewRow.detach();

            // Hide buttons
            if(newRows.length === 1) {
                buttons.find("[data-action=save]").addClass("bz-tablet-grid__buttons--hidden");
                buttons.find("[data-action=cancel]").addClass("bz-tablet-grid__buttons--hidden");
            }

            // Show table component if there are no rows
            if (savedRows.length === 0) {
                self._getComponent("table").hide();
                self._getComponent("emptyTable").show();
            }

            return self._triggerHandler("cancelEdition", {keys: self.getNewRowKeys(cancelNewRow)});
        },

        /**
         * Replaces a matched element in the container for the specified "replace" element
         * @param html
         * @param element
         * @param replace
         * @returns {*}
         * @private
         */
        _replaceElement: function (html, element, replace) {
            return html.replace(new RegExp("{{element " + element + "}}", 'g'), replace);
        }

    });

})(jQuery);
