/*
*   Name: Bizagi Render Search List Control
*   Author: UI Forms Team
*/

$.Class.extend("bizagi.rendering.searchList", {}, {

    /**
     * Update or init the element data
     * @param {} data 
     * @returns {} 
     */
    initializeData: function(data) {
        var self = this;

        // Call base
        self._super(data);

        // Fill default properties
        var properties = this.properties;
        properties.maxRecords = Number(properties.maxRecords) || 20;
        properties.editable = bizagi.util.parseBoolean(properties.editable) !== null ? bizagi.util.parseBoolean(properties.editable) : true;

        // Set value
        self.setValue(properties.value);
    },

    /**
     * Template method to implement in each children to customize each control
     * @returns {} 
     */
    renderControl: function() {
        var self = this;
        var properties = self.properties;
        var template = self.renderFactory.getTemplate("searchList");

        return $.fasttmpl(template, {
            id: properties.id,
            xpath: properties.xpath,
            value: properties.value,
            allowTyping: properties.allowTyping,
            items: properties.value,
            hasSearchForm: properties.searchForms ? true : false,
            editable: properties.editable
        }, {
            formatItem: self.formatItem
        });
    },

    /**
     * Method to render non editable values
     * @returns {} 
     */
    renderReadOnly: function() {
        return this.renderControl();
    },

    /**
     * Sets the internal value
     * @param {} value 
     * @returns {} 
     */
    setValue: function(value) {
        this.value = JSON.encode({ "value": value });
        this.triggerRenderChange();
    },

    /**
     * Add elements to global value
     * @param value = { id:integer,value:[string || array] }
     * @returns {} 
     */
    addElement: function(value) {
        var list = JSON.parse(this.value);

        list.value.push(value);

        this.setValue(list.value);
    },

    /**
     * Remove elements to global value
     * @param value = { id:integer }
     * @returns {} 
     */
    removeElement: function(value) {
        var list = JSON.parse(this.value);
        var result = [];

        $.each(list.value, function(key, val) {
            if (val.id != value.id) {
                result.push({ id: val.id, value: val.value });
            }
        });

        this.setValue(result);
    },

    /**
     * Remove last element to global value
     * @returns {} 
     */
    removeLastElement: function() {
        var list = JSON.parse(this.value);

        if (list.value.length > 0) {
            list.value.pop();
            this.setValue(list.value);
        }
    },

    /**
     * Fetch the data into a deferred
     * @param {} params 
     * @returns {} 
     */
    getData: function(params) {
        var self = this;
        var properties = self.properties;
        var defer = new $.Deferred();

        // Resolve with remote data
        self.dataService.getSearchData({
            url: properties.dataUrl,
            xpath: properties.xpath,
            idRender: properties.id,
            xpathContext: properties.xpathContext,
            idPageCache: properties.idPageCache,
            term: properties.term,
            extra: params
        }).done(function(data) {
            // Resolve with fetched data
            properties.data = data;
            defer.resolve(properties.data);
        }).fail(function(data) {
            var hideStatus = data.hideStatus || false;

            defer.resolve({});

            if (!hideStatus) {
                self.trigger("onSearchError", data);
            }
        });

        return defer.promise();
    },

    /**
     * Submits the selected data to the server in order to be saved
     * @returns {} 
     */
    submitData: function() {
        var data = {};

        // Add current data
        data[this.properties.xpath] = this.getValue();

        // Executes submit on change
        this.submitOnChange(data);
    },

    /**
     * Method to determine if the render value can be sent to the server or not
     * @returns {} 
     */
    canBeSent: function() {
        return true;
    },

    /**
     * Check if the control has value
     * @returns {} 
     */
    hasValue: function() {
        var list = this.getListItems();

        return list.length > 0;
    },

    getListItems: function(){
        var value = this.getValue();
        var finalValue = typeof (value) === "string" ? JSON.parse(value).value : value.value;

        return finalValue || [];
    },
    
    /**
     * Formats value of each item
     * @param {string, boolean or array} value
     * @example
     * value = "string" => "string"
     * value = true => "yes" or "si", the localized value
     * value = false => "no", the localized value
     * value = ["string 1", "string 2"] => "string 1 - string 2"
     * @returns {string}
     */
    formatItem: function (value) {
        return bizagi.util.formatItem(value);
    }

});
