/**
 *   Name: Bizagi Render MultiSelect Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for multiselect renders
 */

$.Class.extend("bizagi.rendering.multiSelect", {}, {

    /**
     * Update or init the element data
     * @param data
     */
    initializeData: function (data) {
        // Call base
        this._super(data);
        if (Array.isArray(this.value)) {
            this.value = this.properties.value = JSON.encode({"value": this.value});
            this.properties.originalValue = this.properties.previousValue = this.value;
        }
        // Fill default properties
        var properties = this.properties;
        properties.editable = bizagi.util.parseBoolean(properties.editable) !== null ? bizagi.util.parseBoolean(properties.editable) : true;
        properties.maxRecords = Number(properties.maxRecords) || 20;
    },

    /**
     * Template method to implement in each children to customize each control
     * @returns {*|jQuery}
     */
    renderControl: function () {
        var self = this;
        var properties = self.properties;
        var template = self.renderFactory.getTemplate("list-multiselect");

        return $.fasttmpl(template, {
            id: properties.id,
            xpath: properties.xpath,
            editable: properties.editable,
            align: properties.valueAlign
        }, {
            formatItem: self.formatItem
        });
    },

    /**
     * Method to render non editable values
     */
    renderReadOnly: function () {
        var self = this;
        var properties = self.properties;
        var template = self.renderFactory.getTemplate("list-multiselect");

        return $.fasttmpl(template, {
            id: properties.id,
            xpath: properties.xpath,
            editable: properties.editable,
            align: properties.valueAlign
        }, {
            formatItem: self.formatItem
        });
    },

    /**
     * Fetch the data into a deferred
     * @returns {*[]}
     */
    getData: function () {
        var self = this;
        var defer = new $.Deferred();
        var properties = this.properties;

        // Go to server
        self.dataService.multiaction().getData({
            url: properties.dataUrl,
            xpath: properties.xpath,
            idRender: properties.id,
            xpathContext: properties.xpathContext,
            contexttype: properties.contexttype || null,
            idPageCache: properties.idPageCache
        }).done(function (data) {
            properties.data = self.normalizeData(data);

            defer.resolve(properties.data);

        }).fail(function (data) {
            if (data.type === "not-processed")
                return;

            // Show error from server
            var auxMsg = "";
            var form = self.getFormContainer();
            var msgObj = self.properties.displayName + ": ";

            if (typeof data == "string") {
                auxMsg = JSON && JSON.parse(data) || $.parseJSON(data);
            } else {
                auxMsg = data;
            }

            msgObj += auxMsg.message;

            form.addValidationMessage(msgObj);

            defer.reject(data);
        });

        return defer.promise();
    },

    /**
     * Sets the internal value
     * @param value
     * @param triggerEvents
     */
    setValue: function (value, triggerEvents) {
        triggerEvents = triggerEvents !== undefined ? triggerEvents : true;

        if(Array.isArray(value)){		
			value = JSON.encode({"value": value});
		} else {
			value = value;
		}

        this._super(value, triggerEvents);
    },

    /**
     * Sets the value in the rendered control
     * @param value
     */
    setDisplayValue: function (value) {
        var self = this;

        // Set internal value
        self.setValue(value, false);
    },

    /**
     * Method to determine if the render value can be sent to the server or not
     * @returns {boolean}
     */
    canBeSent: function () {
        return true;
    },

    /**
     * Returns the internal value
     * @returns {*|Array}
     */
    getValue: function () {
        return this.value || [];
    },

    /**
     * Get list of data
     * @returns {*|Array}
     */
    getDataValue: function () {
        var list;

        try {
            list = JSON.parse(this.value);

            return list.value || [];
        } catch (e) {
            return [];
        }
    },

    /**
     * Check if a render has no value for required validation
     * @returns {*|Array|boolean}
     */
    hasValue: function () {
        var values = this.getDataValue();

        return values && values.length > 0;
    },

    getElementsToStylize: function (context) {
        return $(".bz-multiselect .bz-multiselect__tag:not(.bz-multiselect__plus)", context);
    },

    /**
     * Add elements to global value
     * @param value
     */
    addElement: function (value) {
        var newValue = this.getDataValue();

        newValue.push(value);

        this.setValue(newValue);
    },

    /**
     * Remove elements to global value
     * @param value
     */
    removeElement: function (value) {
        var idToRemove = value.id;
        var newValue = this.getDataValue()
            .filter(function (item) {
                return item.id !== idToRemove;
            });

        this.setValue(newValue);
    },

    /**
     * Submit the selected data to the server in order to be saved
     */
    submitData: function () {
        var data = {};

        // Add current data
        data[this.properties.xpath] = this.getValue();

        // Executes submit on change
        this.submitOnChange(data);
    },

    /**
     * Mark a item how checked
     * @param id
     * @returns {*}
     */
    markAsChecked: function (id) {
        var self = this;

        return self.selected.some(function (item, index) {
            var found = item.id === id;

            if (found) self.selected.splice(index, 1);
            return found;
        });
    },

    isChecked: function (values, id) {
        return values.some(function (item) {
            return item.id == id;
        });
    },

    /**
     * Method to verify if can add more items
     * @param $tagsContainer
     * @returns {boolean}
     */
    canAddMoreItems: function ($tagsContainer) {
        var self = this;
        var containerWidth = parseInt($tagsContainer.css("width"));
        var $children = $tagsContainer.children();

        var childrenWidth = $children
            .toArray()
            .reduce(function (acc, child) {
                acc += self.getElementWidth(child);
                return acc;
            }, 0);

        return containerWidth > (childrenWidth + self.Class.MORE_OPTION_WIDTH)

    },

    /**
     * getElementWidth
     * @param element
     * @returns {*}
     */
    getElementWidth: function (element) {
        var styles = window.getComputedStyle(element);
        var outerWidth = $(element).outerWidth();
        return outerWidth + [
            "padding-left",
            "padding-right",
            "margin-left",
            "margin-right"
        ].reduce(function (acc, property) {
            acc += parseInt(styles[property]);
            return acc;
        }, 0);
    },

    /**
     * Filtering list of elements
     * @param query
     * @param $container
     */
    filterList: function (query, $container) {

        $container.filter(function () {
            var stringContainer = $(this).text().toUpperCase();
            var stringToSearch = query.toUpperCase();

            if (stringContainer.indexOf(stringToSearch) >= 0) {
                return $(this);
            } else {
                $(this).hide();
            }
        }).show();

    },

    /**
     * addSelectedElements
     */
    addSelectedElements: function () {
    },

    /**
     * Find item by id
     * @param id
     * @returns {[]}
     */
    findItemById: function (id) {
        return (this.properties.data || []).find(function (item) {
            return item.id == id;
        });
    },

    /**
     * Formats value of each item
     * @param {string, boolean or array} value
     * @example
     * value = "string" => "string"
     * value = true => "yes" or "si", the localized value
     * value = false => "no", the localized value
     * value = ["string 1", "string 2"] => "string 1 - string 2"
     * @returns {string}
     */
    formatItem: function (value) {
        return bizagi.util.formatItem(value);
    },

    /**
     * Complement data from server before control implementation
     * @param data
     */
    normalizeData: function (data) {
        (data || []).forEach(function (item) {
            item.value = typeof (item.value) !== "undefined" && item.value !== null ? item.value : "";
        });

        return data;
    }

});
