/**
 *   Name: Bizagi Render Text Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for text renders
 */

$.Class.extend("bizagi.rendering.text", {}, {

    /**
     * Update or init the element data
     * @param data
     */
    initializeData: function (data) {
        // Call base
        this._super(data);

        // Fill default properties
        var properties = this.properties;

        if (properties.regularExpressionExpression) {
            var message = properties.regularExpressionMessage || this.getResource("render-text-regular-expression-default-message");
            properties.regularExpression = {"expression": properties.regularExpressionExpression, "message": message};
        }
    },

    /**
     * Template method to implement in each children to customize each control
     * @returns {*}
     */
    renderControl: function () {
        var template = this.renderFactory.getTemplate("text");

        // Render template
        var html = $.fasttmpl(template, {
            value: this.properties.value,
            isEmptyField: typeof (this.properties.value) === "undefined",
            helpText: this.properties.helpText,
            editable: this.properties.editable
        });

        return html;
    },

    /**
     * Method to render non editable values
     */
    renderReadOnly: function () {
        var template = this.renderFactory.getTemplate("text-read-only");
        var value = this.getValue();

        if (bizagi.util.isString(value)) {
            value = value.replaceAll("`", "&#96;");
        }

        // Render template
        return $.fasttmpl(template, {value: value, editable: this.properties.editable});
    },

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        // Call base
        this._super();

        this.input = this.getControl().find("input");

        //Attach event for retype double
        if (this.properties.retype === "double")
            this.attachRetypeDouble();
    },

    /**
     * function virtual implements on children (desktop,samrtphone...).
     */
    attachRetypeDouble: function () {
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        var self = this;
        var properties = self.properties;

        // Check mask property
        if (properties.mask)
            self.input.mask({mask: properties.mask});

        // Attach change event
        self.input.bind("change", function () {

            // Updates internal value
            self.setValue(self.input.val(), false);

            // Check that the value is valid
            var validationMessages = [];
            if (properties.retype !== "duplicate") {
                if (self.isValid(validationMessages) || typeof (properties.regularExpression) === "undefined") {
                    // Update value again triggering handlers
                    self.setValue(self.input.val(), true);
                }
            }

        }).keypress(function (e) {
            e = window.event || e;
            var keyUnicode = e.charCode || e.keyCode;

            if (e !== undefined) {
                if (keyUnicode == 13) {
                    $(this).trigger("change");
                }
            }
        });
    },

    /**
     * Public method to determine if a value is valid or not
     * @param invalidElements
     * @returns {*}
     */
    isValid: function (invalidElements) {
        var self = this;
        var properties = self.properties;
        // Call base
        var bValid = this._super(invalidElements);
        var value = self.getValue();

        // When the form have big data, block browser executing RegExp. QAF-1418.
        // Check regular expression
        if (typeof value == "string") {
            if (properties.regularExpression) {
                var regex = new RegExp("");
                try {
                    regex = new RegExp(properties.regularExpression.expression);
                } catch (e) {
                    if (bizagi.override.showDesignValidations) {
                        bizagi.log(e);
                        invalidElements.push({xpath: properties.xpath, message: e.message});
                        bValid = false;
                    }
                }

                if (value && ((value.match(new RegExp(".*?(<|\\u003C)(.+?)(>|\\u003E)(.?)|eval\\(|eval\\u0028", "g"))) || (!value.match(regex) && self.properties && self.properties.xpath === "userName"))) {
                    var message = properties.regularExpression.message;
                    invalidElements.push({xpath: properties.xpath, message: message});
                    bValid = false;
                } else {
                    if (value && ((value.match(new RegExp(".*?(<|\\u003C)(.+?)(>|\\u003E)(.?)|eval\\(|eval\\u0028", "g"))) || (!value.match(regex)))) {
                        var message = properties.regularExpression.message;
                        invalidElements.push({xpath: properties.xpath, message: message});
                        bValid = false;
                    }
                }
            } else {
                // Strange behavior on IE. When matchs are united on IE have is very slow
                // solution, divide matchs
                //.*? means for more information search: "Lazy matching regexp"
                if (value && (value.match(new RegExp(".*?eval\\(", "g"))) && (value.match(new RegExp(".*?eval\\u0028", "g")))) {
                    if (properties.editable) {
                        var message = this.getResource("render-text-regular-expression-default-message");

                        invalidElements.push({xpath: properties.xpath, message: message});
                        bValid = false;
                    }
                }
            }
        }

        return bValid;
    },

    /**
     * Sets the value in the rendered control
     * @param value
     */
    setDisplayValue: function (value) {
        var self = this;
        var control = self.getControl();
        var decodedValue = bizagi.util.decodeURI(value);

        if (!bizagi.util.parseBoolean(self.properties.editable)) {
            // Render as simple value
            if (typeof (value) === "string") {
                // Replace line breaks for html line breaks
                var valueToDisplay = bizagi.util.decodedDisplayValue(self.getDisplayValue());

                control.html("<label class='readonly-control'>" + valueToDisplay + "</label>");
            } else {
                if (bizagi.util.isNull(value)) {
                    control.html("");
                }
            }
        }

        // Set internal value
        self.setValue(decodedValue, false);
    },

    /**
     * Extend setValue to fix all encode data from database
     * @param value
     */
    setValue: function (value) {
        this._super(value);
    },

    /**
     * Helptext tooltip
     * @param {} control
     * @param {} currentValue
     * @param {} currentTitle
     */
    createTooltip: function (control, currentValue, currentTitle) {
        control.data("oldValue", currentValue);
        control.val("");
        
        if(control.data('ui-tooltip')) control.tooltip('destroy');  
        
        control.attr("title", currentTitle);
        control.tooltip();

        var toolTipZindex = $.getMaxZindex();
        control.tooltip().on("tooltipopen", function (event, ui) {
            ui.tooltip.zIndex(toolTipZindex);
        });

        // Create new tooltip
        control.tooltip("open");

        setTimeout(function () {
            control.focus();
        }, 100);
    },


    //#region No side effects funtions    
    /**
     * Gets the string with the correct size
     * @param {string} value String value to validate
     * @param {Int} maxLength Maximum size of the string
     * @returns {string} String value validated
     */
    validateValue: function (value, maxLength) {
        if (maxLength < 1 || value.length <= maxLength) return value;
        return value.substr(0, maxLength)
    }
    //#endregion No side effects funtions

});