/**
 *   Name: Bizagi Render Text Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for text renders
 */

$.Class.extend("bizagi.rendering.extendedText", {}, {

    /**
     * Update or init the element data
     * @param {} data
     */
    initializeData: function (data) {
        var self = this;

        // Call base
        self._super(data);

        // Check if offline forms is supported
        this.isOfflineForm = bizagi.util.isOfflineForm({context: self});
        this.hasMaxLines = this.properties.maxLines ? true : false;

        // Fill default properties
        var properties = this.properties;

        // Fill default properties
        properties.maxLines = this.properties.maxLines || 6;

        if (properties.regularExpressionExpression) {
            var message = properties.regularExpressionMessage || self.getResource("render-text-regular-expression-default-message");

            properties.regularExpression = {"expression": properties.regularExpressionExpression, "message": message};
        }
    },

    /**
     * Template method to implement in each children to customize each control
     */
    renderControl: function () {
        var template = this.renderFactory.getTemplate("extendedText");

        // Render template
        var html = $.fasttmpl(template, {
            isEmptyField: typeof (this.properties.value) === "undefined",
            editable: this.properties.editable
        });

        return html;
    },

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        var self = this;
        self._super();

        var control = self.getControl();
        self.textarea = control.find("textarea");

        // If is textarea, then enable resize element.
        if (self.textarea) {
            self.textarea.css({resize: "both"});
        } else {
            self.textarea.css({resize: "none"});
        }

        // Attach event for retype double
        if (self.properties.retype === "double")
            self.attachRetypeDouble();
    },

    /**
     * Function virtual implements on children (desktop,samrtphone...).
     */
    attachRetypeDouble: function () {
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        var self = this;
        var properties = self.properties;

        // Set the default maxlength for the input if is 0 or undefined set to infinity
        // this resolve issue with scope strings that don't have maxlength
        if (properties.maxLength)
            self.textarea.prop('maxlength', properties.maxLength);

        if (properties.maxLines) {
            self.textarea.prop('rows', properties.maxLines)
        } else {
            self.textarea.prop('rows', 6);
        }

        // Attach change event
        self.textarea.bind("change", function () {

            // Updates internal value
            self.setValue(self.textarea.val());

            // Check that the value is valid
            var validationMessages = [];
            if (properties.retype !== "duplicate") {
                if (self.isValid(validationMessages) || typeof (properties.regularExpression) === "undefined") {
                    // Update value again triggering handlers
                    self.setValue(self.textarea.val(), true);
                } else {
                    // Set error message
                    var message = validationMessages[0].message;
                    bizagi.showMessageBox(message, "Bizagi", "error");
                }
            }
        });

        // Attach blur event
        self.textarea.bind("blur", function () {
            // Updates internal value
            self.setValue(self.textarea.val());
        });
    },

    /**
     * Public method to determine if a value is valid or not
     * @param {} invalidElements
     * @returns {}
     */
    isValid: function (invalidElements) {
        var self = this;
        var properties = self.properties;
        var message;

        // Call base
        var bValid = this._super(invalidElements);
        var value = self.getValue();
        var regEx = new RegExp(".*?(<|\\u003C)(.+?)(>|\\u003E)(.?)|eval\\(|eval\\u0028", "g");

        // Offline capability
        if (this.isOfflineForm && bizagi.util.isMobileDevice()) {
            value = value && typeof (value) === "object" && value.value ? value.value : value;
        }

        // Check regular expression
        if (properties.regularExpression) {

            if (value && ((value.match(regEx)) || (!value.match(new RegExp(properties.regularExpression.expression)) && (self.properties && self.properties.xpath === "userName")))) {
                message = properties.regularExpression.message;
                invalidElements.push({xpath: properties.xpath, message: message});
                bValid = false;
            } else {
                if (value && ((value.match(regEx)) || (!value.match(new RegExp(properties.regularExpression.expression))))) {
                    message = properties.regularExpression.message;
                    invalidElements.push({xpath: properties.xpath, message: message});
                    bValid = false;
                }
            }
        } else {
            if (value && (value.match(new RegExp(".*?eval\\(", "g"))) && (value.match(new RegExp(".*?eval\\u0028", "g")))) {
                if (properties.editable) {
                    message = this.getResource("render-text-regular-expression-default-message");
                    invalidElements.push({xpath: properties.xpath, message: message});
                    bValid = false;
                }
            }
        }

        return bValid;
    },

    /**
     * Sets the value in the rendered control
     * @param {} value
     * @returns {}
     */
    setDisplayValue: function (value) {
        var self = this;
        var control = self.getControl();
        var decodedValue = bizagi.util.decodeURI(value);

        if (self.properties.editable == false) {
            // Render as simple value
            if (typeof (value) == "string") {

                // Replace line breaks for html line breaks
                var valueToDisplay = bizagi.util.decodedDisplayValue(self.getDisplayValue());

                control.html(valueToDisplay);
            }
        }

        // Set internal value
        self.setValue(decodedValue, false);
    },

    /**
     * Add the render data to the given collection in order to send data to the server
     * @param {} renderValues
     * @returns {}
     */
    collectData: function (renderValues) {
        // Call base
        this._super(renderValues);
    },

    /**
     * Extend setValue to fix all encode data from database
     * More information SUITE-9407
     * @param {} value
     * @returns {}
     */
    setValue: function (value) {
        var decodedValue = bizagi.util.decodeURI(value);

        this._super(decodedValue);
    },

    /**
     * Helptext tooltip
     * @param {} control
     * @param {} currentValue
     * @param {} currentTitle
     */
    createTooltip: function (control, currentValue, currentTitle) {
        control.data("oldValue", currentValue);
        control.val("");

        if(control.data('ui-tooltip')) control.tooltip('destroy');  

        control.attr("title", currentTitle);
        control.tooltip();

        var toolTipZindex = $.getMaxZindex();
        control.tooltip().on("tooltipopen", function (event, ui) {
            ui.tooltip.zIndex(toolTipZindex);
        });

        // Create new tooltip
        control.tooltip("open");

        setTimeout(function () {
            control.focus();
        }, 100);
    },

    //#region No side effects functions
    /**
     * Gets the string with the correct size
     * @param {string} value String value to validate
     * @param {Int} maxLength Maximum size of the string
     * @returns {string} String value validated
     */
    validateValue: function (value, maxLength) {
        if(maxLength<1 || value.length<=maxLength)return value;
        return value.substr(0, maxLength)
    }
    //#endregion No side effects functions
});
